


























































































import {Vue, Component, Watch} from 'vue-property-decorator'
import CommonButton from "~/components/common/CommonButton.vue";

@Component({
    components: {
      CommonButton,
      Login: () => import('@/components/user/Login.vue')
    }
  }
)
export default class User extends Vue {
  $axios: any;
  show_user_setting: boolean = false
  show_user_menu: boolean = false
  config: any = JSON.parse(this.$store.getters['user/config'])
  add_open_url_obj: object = {
    start_time: '',
    end_time: '',
    open_url: '',
  }
  url_list: [] = []
  default_search_engine: number = 2

  mounted(): void {
    console.log('this.config:', this.config)
    // this.config = JSON.parse(this.$store.getters['user/config'])

    if (!this.config.front_config) {
      this.config.front_config = {}
    }
    // 特定时间打开网站
    if (this.config.front_config.open_url_list === undefined) {
      this.config.front_config.open_url_list = []
    }

    this.url_list = this.config.front_config.open_url_list
    let time_now = new Date().getTime()
    this.config.front_config.open_url_list.forEach((item: any) => {
      if (time_now > this.formate_time(item.start_time) && time_now < this.formate_time(item.end_time)) {
        window.open(item.open_url)
      }
    })

    // 修改默认搜索引擎
    if (this.config.front_config.default_search_engine === undefined) {
      this.config.front_config.default_search_engine = 2
    }
    this.default_search_engine = this.config.front_config.default_search_engine
  }

  get avatar() {
    return this.$store.state.user.info ? this.$store.state.user.info.avatar : ''
  }

  formate_time(time: string) {
    let targget_time: string = new Date().toLocaleDateString() + ' ' + time
    return new Date(targget_time).getTime()
  }

  hide() {
    this.show_user_setting = false
  }

  save_config() {
    this.$store.dispatch('user/set_user_config', this.config)
  }

  del_open_url(i: number) {
    this.config.front_config.open_url_list.splice(i, 1)
  }

  add_open_url() {
    this.config.front_config.open_url_list.push(this.add_open_url_obj)
    this.add_open_url_obj = {
      start_time: '',
      endtime: '',
      open_url: '',
    }
  }

  to_login() {
    location.href += 'account'
  }

  on_user_menu_mouseenter() {
    if (this.$store.getters['user/logined']) {
      this.show_user_menu = true
    }
  }

  on_user_menu_mouserleave() {
    setTimeout(() => {
      this.show_user_menu = false
    }, 200)
  }

  async logout() {
    await this.$axios.get('/xz_server/user/logout')
    localStorage.removeItem('HomePageStorage')
    location.reload()
  }

  @Watch('default_search_engine')
  setNewEngin(newVal: number) {
    this.config.front_config.default_search_engine = newVal
  }

  // @Watch('config',{ deep: true })
  // change_config(){
  //   this.save_config()
  // }
}
